import * as actionsTypes from "../actions/types";

const initialState = {
  attributes: {},
  loading: false,
  itemLoading: false,
  errors: null,
};

export default (state = initialState, action) => {
  switch (action.type) {
    case actionsTypes.ATTRIBUTES_LOADING:
      return {
        ...state,
        loading: true,
        errors: null,
      };
    case actionsTypes.SET_ATTRIBUTES:
      return {
        ...state,
        attributes: {
          attributes: action.attributes.attributes,
          pagination: action.attributes.pagination,
        },
        loading: false,
      };
    case actionsTypes.ATTRIBUTES_SWITCH:
      const currAttributes = state.attributes.attributes;
      const id = action.id;
      const status = action.status;
      const updatedStatusAtts = currAttributes.map((att) => {
        if (att.attribute_id === id) {
          return {
            ...att,
            status: status,
          };
        }
        return att;
      });
      return {
        ...state,
        attributes: {
          ...state.attributes,
          attributes: updatedStatusAtts,
        },
        itemLoading: false,
      };
    case actionsTypes.ATTRIBUTES_DELETE:
      const thisAttributes = state.attributes.attributes;
      const filteredAtts = thisAttributes.filter(
        (att) => att.attribute_id !== action.id
      );
      return {
        ...state,
        attributes: {
          ...state.attributes,
          attributes: filteredAtts,
        },
        itemLoading: false,
      };
    case actionsTypes.ATTRIBUTES_ERRORS:
      return {
        ...state,
        loading: false,
        errors: action.errors,
      };
    case actionsTypes.ITEM_LOADING:
      return {
        ...state,
        itemLoading: true,
      };
    case actionsTypes.UNLOAD:
      return {
        ...state,
        loading: false,
        itemLoading: false,
      };
    default:
      return state;
  }
};
