import * as actionTypes from "./types";
import { axios } from "../../util/axiosAuth";

const loading = () => {
  return {
    type: actionTypes.CATEGORIES_LOADING,
  };
};
const itemLoading = () => {
  return {
    type: actionTypes.ITEM_LOADING,
  };
};
const setErrors = (err) => {
  return {
    type: actionTypes.CATEGORIES_ERRORS,
    errors: err,
  };
};

export const getCategories = (query) => {
  return async (dispatch) => {
    dispatch(loading());

    try {
      let url = `/categories?expand=children`;

      for (const key in query) {
        url += `&${key}=${query[key]}`;
      }

      const res = await axios.get(url);
      dispatch({ type: actionTypes.SET_CATEGORIES, categories: res.data.data });
      // return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    }
  };
};
export const getParentCategories = () => {
  return async (dispatch) => {
    dispatch(itemLoading());
    try {
      let url = `/categories`;

      const res = await axios.get(url);
      return res.data.data.categories;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    } finally {
      dispatch({ type: actionTypes.UNLOAD });
    }
  };
};
export const getCategory = (id) => {
  return async (dispatch) => {
    dispatch(loading());
    try {
      let url = `/categories/${id}`;
      const res = await axios.get(url);
      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    } finally {
      dispatch({ type: actionTypes.UNLOAD });
    }
  };
};

export const addEditCategory = (body, isEdit) => {
  return async (dispatch) => {
    dispatch(loading());

    try {
      let url = "/categories";

      const data = new FormData();
      if (body.image.length) {
        for (const file of body.image) {
          data.append("image", file);
        }
      }

      //Keep image path when edit
      if (isEdit) {
        let prevImage = [];
        for (const img of body.image) {
          if (img.id.toString().startsWith("prev_")) {
            prevImage.push(img.name);
          }
        }
        data.append("prevImg", JSON.stringify(prevImage));
      }

      delete body.image;
      for (const key in body) {
        data.append(key, body[key]);
      }

      let res;
      if (isEdit) {
        res = await axios.put(`${url}/${isEdit}`, data);
      } else {
        res = await axios.post(url, data);
      }

      // dispatch({ type: actionTypes.SET_PRODUCTS, products: res.data.data });
      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    } finally {
      dispatch({ type: actionTypes.UNLOAD });
    }
  };
};

export const switchCategoryStatus = (id, status) => {
  return async (dispatch) => {
    dispatch(itemLoading());
    try {
      let url = `/categories/${id}`;
      const res = await axios.patch(url, { status });
      dispatch({ type: actionTypes.CATEGORIES_SWITCH, id, status });
      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    } finally {
      dispatch({ type: actionTypes.UNLOAD });
    }
  };
};

export const deleteCategory = (id) => {
  return async (dispatch) => {
    dispatch(itemLoading());
    try {
      let url = `/categories/${id}`;
      const res = await axios.delete(url);
      dispatch({ type: actionTypes.CATEGORIES_DELETE, id: id });
      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    } finally {
      dispatch({ type: actionTypes.UNLOAD });
    }
  };
};

let timeout;
export const categoriesAutocomplete = (q) => {
  return async (dispatch) => {
    dispatch(loading());
    clearTimeout(timeout);

    try {
      let url = "/categories/autocomplete?q=";
      if (q) {
        url += q;
      }
      const promise = new Promise((resolve, reject) => {
        timeout = setTimeout(async () => {
          resolve(axios.get(url));
        }, 1000);
      });
      const res = await promise;
      dispatch({ type: actionTypes.UNLOAD });
      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    }
  };
};
