import * as actionTypes from "./types";
import { axios } from "../../util/axiosAuth";

const loading = () => {
  return {
    type: actionTypes.ATTRIBUTES_LOADING,
  };
};
const itemLoading = () => {
  return {
    type: actionTypes.ITEM_LOADING,
  };
};
const setErrors = (err) => {
  return {
    type: actionTypes.ATTRIBUTES_ERRORS,
    errors: err,
  };
};

export const getAttributes = (query) => {
  return async (dispatch) => {
    dispatch(loading());

    try {
      let url = `/attributes`;

      let counter = 1;
      for (const key in query) {
        let operator = counter > 1 ? "&" : "?";
        url += `${operator}${key}=${query[key]}`;
        counter++;
      }
      const res = await axios.get(url);
      dispatch({ type: actionTypes.SET_ATTRIBUTES, attributes: res.data.data });
      // return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    }
  };
};

export const getAttribute = (id) => {
  return async (dispatch) => {
    dispatch(loading());
    try {
      let url = `/attributes/${id}`;
      const res = await axios.get(url);
      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    } finally {
      dispatch({ type: actionTypes.UNLOAD });
    }
  };
};

export const addEditAttribute = (body, isEdit) => {
  return async (dispatch) => {
    dispatch(loading());

    try {
      let url = "/attributes";

      let res;
      if (isEdit) {
        res = await axios.put(`${url}/${isEdit}`, body);
      } else {
        res = await axios.post(url, body);
      }

      // dispatch({ type: actionTypes.SET_PRODUCTS, products: res.data.data });
      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    } finally {
      dispatch({ type: actionTypes.UNLOAD });
    }
  };
};

export const switchAttributeStatus = (id, status) => {
  return async (dispatch) => {
    dispatch(itemLoading());
    try {
      let url = `/attributes/${id}`;
      const res = await axios.patch(url, { status });
      dispatch({ type: actionTypes.ATTRIBUTES_SWITCH, id, status });
      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    } finally {
      dispatch({ type: actionTypes.UNLOAD });
    }
  };
};

export const deleteAttribute = (id) => {
  return async (dispatch) => {
    dispatch(itemLoading());
    try {
      let url = `/attributes/${id}`;
      const res = await axios.delete(url);
      dispatch({ type: actionTypes.ATTRIBUTES_DELETE, id: id });
      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    } finally {
      dispatch({ type: actionTypes.UNLOAD });
    }
  };
};

export const attributesAutocomplete = (q) => {
  return async (dispatch) => {
    dispatch(loading());
    try {
      let url = "/attributes/autocomplete?q=";
      if (q) {
        url += q;
      }
      const res = await axios.get(url);
      dispatch({ type: actionTypes.UNLOAD });
      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    }
  };
};
