import * as actionsTypes from "../actions/types";

const initialState = {
  filters: {},
  loading: false,
  itemLoading: false,
  errors: null,
};

let flattedFilters = [];

export default (state = initialState, action) => {
  switch (action.type) {
    case actionsTypes.FILTERS_LOADING:
      return {
        ...state,
        loading: true,
        errors: null,
      };
    case actionsTypes.SET_FILTERS:
      flattedFilters = [];
      const filters = falttenFilters(action.filters.filters);
      return {
        ...state,
        filters: {
          filters: filters,
          pagination: action.filters.pagination,
        },
        loading: false,
      };
    case actionsTypes.FILTERS_SWITCH:
      const currentFilters = state.filters.filters;
      const id = action.id;
      const status = action.status;
      const updatedStatusFilters = currentFilters.map((fil) => {
        if (fil.filter_id === id) {
          return {
            ...fil,
            status: status,
          };
        }
        return fil;
      });
      return {
        ...state,
        filters: {
          ...state.filters,
          filters: updatedStatusFilters,
        },
        itemLoading: false,
      };
    case actionsTypes.FILTERS_DELETE:
      const thisFilters = state.filters.filters;
      const filteredFilters = thisFilters.filter(
        (fil) => fil.filter_id !== action.id && fil.parent_id !== action.id
      );
      return {
        ...state,
        filters: {
          ...state.filters,
          filters: filteredFilters,
        },
        itemLoading: false,
      };
    case actionsTypes.FILTERS_ERRORS:
      return {
        ...state,
        loading: false,
        errors: action.errors,
      };
    case actionsTypes.ITEM_LOADING:
      return {
        ...state,
        itemLoading: true,
      };
    case actionsTypes.UNLOAD:
      return {
        ...state,
        loading: false,
        itemLoading: false,
      };
    default:
      return state;
  }
};

let currentParent = "";
const falttenFilters = (filters, child) => {
  for (const filter of filters) {
    let _filter = { ...filter };
    if (child) {
      _filter.title = currentParent + _filter.title;
    }
    flattedFilters.push(_filter);
    if (filter.children && filter.children.length) {
      currentParent = filter.title + " > ";
      falttenFilters(filter.children, true);
    }
  }
  return flattedFilters;
};
