import React from "react";
import { Paper, Typography, useTheme } from "@material-ui/core";
import TableSkeleton from "../../common/TableSkeleton";
import { useTranslation } from "react-i18next";
import { Chart } from "react-google-charts";
import i18n from "../../../i18n";

const SalesChart = ({ sales, dateType }) => {
  const { t } = useTranslation();
  const theme = useTheme();
  return (
    <Paper className="xp fullheight">
      <Typography align="center" variant="h4">
        {t("common:sales_chart")}
      </Typography>
      <Chart
        width="100%"
        height="300px"
        // style={{ height: "100%" }}
        chartType="ColumnChart"
        loader={<TableSkeleton row={1} col={1} />}
        data={[
          ["Year", t("common:sales")],
          ...sales.map((s) => [s.vAxis + "", +s.value]),
        ]}
        options={{
          // title: "Company Performance",
          legend: {
            position: "none",
            textStyle: { fontFamily: "Cairo" },
          },
          hAxis: {
            title: t(`common:${dateType.type}_xaxis`, {
              year: dateType.date.split(",")[0],
              month: dateType.date.split(",")[1],
            }),
          },
          vAxis: {
            title: t("common:sales"),
            minValue: 0,
          },
          colors: [theme.palette.primary.main],
          animation: {
            duration: 1000,
            easing: "out",
            startup: true,
          },
        }}
        chartLanguage={i18n.language}
        // For tests
        rootProps={{ "data-testid": "2" }}
      />
    </Paper>
  );
};

export default SalesChart;
