import * as actionsTypes from "../actions/types";

const initialState = {
  users: {},
  loading: false,
  itemLoading: false,
  errors: null,
};

export default (state = initialState, action) => {
  switch (action.type) {
    case actionsTypes.USERS_LOADING:
      return {
        ...state,
        loading: true,
        errors: null,
      };
    case actionsTypes.SET_USERS:
      return {
        ...state,
        users: {
          users: action.users.users,
          pagination: action.users.pagination,
        },
        loading: false,
      };
    case actionsTypes.USERS_SWITCH:
      const currUsers = state.users.users;
      const id = action.id;
      const status = action.status;
      const updatedUsers = currUsers.map((user) => {
        if (user.user_id === id) {
          return {
            ...user,
            status: status,
          };
        }
        return user;
      });
      return {
        ...state,
        users: {
          ...state.users,
          users: updatedUsers,
        },
      };

    case actionsTypes.USERS_ERRORS:
      return {
        ...state,
        loading: false,
        errors: action.errors,
      };
    case actionsTypes.ITEM_LOADING:
      return {
        ...state,
        itemLoading: true,
      };
    case actionsTypes.UNLOAD:
      return {
        ...state,
        loading: false,
        itemLoading: false,
      };
    default:
      return state;
  }
};
