import {
  Avatar,
  Chip,
  Divider,
  List,
  ListItem,
  ListItemAvatar,
  ListItemSecondaryAction,
  ListItemText,
  Typography,
} from "@material-ui/core";
import React, { Fragment } from "react";
import Globals from "../../../util/globals";

const RankList = ({ data, color, headline, wAvatar }) => {
  return (
    <List dense>
      <Typography align="center" variant="h6">
        {headline}
      </Typography>
      {data.map((v, i) => (
        <Fragment key={i}>
          <ListItem>
            <ListItemAvatar>
              {wAvatar ? (
                <Avatar alt={v.title} src={`${Globals.server}/${v.image[0]}`} />
              ) : (
                <Avatar>{v.title.charAt(0)}</Avatar>
              )}
            </ListItemAvatar>
            <ListItemText primary={v.title} secondary={v.subtitle} />
            <ListItemSecondaryAction>
              <Chip
                label={v.value}
                size="small"
                color={color || "primary"}
                //   variant="outlined"
              />
            </ListItemSecondaryAction>
          </ListItem>
          {i < data.length - 1 && <Divider variant="inset" />}
        </Fragment>
      ))}
    </List>
  );
};

export default RankList;
