import * as actionTypes from "./types";
import { axios } from "../../util/axiosAuth";

const loading = () => {
  return {
    type: actionTypes.FILTERS_LOADING,
  };
};
const itemLoading = () => {
  return {
    type: actionTypes.ITEM_LOADING,
  };
};
const setErrors = (err) => {
  return {
    type: actionTypes.FILTERS_ERRORS,
    errors: err,
  };
};

export const getFilters = (query) => {
  return async (dispatch) => {
    dispatch(loading());

    try {
      let url = `/filters?expand=children`;

      for (const key in query) {
        url += `&${key}=${query[key]}`;
      }

      const res = await axios.get(url);
      dispatch({ type: actionTypes.SET_FILTERS, filters: res.data.data });
      // return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    }
  };
};
export const getParentFilters = () => {
  return async (dispatch) => {
    dispatch(itemLoading());
    try {
      let url = `/filters`;

      const res = await axios.get(url);
      return res.data.data.filters;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    } finally {
      dispatch({ type: actionTypes.UNLOAD });
    }
  };
};
export const getFilter = (id) => {
  return async (dispatch) => {
    dispatch(loading());
    try {
      let url = `/filters/${id}`;
      const res = await axios.get(url);
      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    } finally {
      dispatch({ type: actionTypes.UNLOAD });
    }
  };
};

export const addEditFilter = (body, isEdit) => {
  return async (dispatch) => {
    dispatch(loading());

    try {
      let url = "/filters";

      let res;
      if (isEdit) {
        res = await axios.put(`${url}/${isEdit}`, body);
      } else {
        res = await axios.post(url, body);
      }

      // dispatch({ type: actionTypes.SET_PRODUCTS, products: res.data.data });
      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    } finally {
      dispatch({ type: actionTypes.UNLOAD });
    }
  };
};

export const switchFilterStatus = (id, status) => {
  return async (dispatch) => {
    dispatch(itemLoading());
    try {
      let url = `/filters/${id}`;
      const res = await axios.patch(url, { status });
      dispatch({ type: actionTypes.FILTERS_SWITCH, id, status });
      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    } finally {
      dispatch({ type: actionTypes.UNLOAD });
    }
  };
};

export const deleteFilter = (id) => {
  return async (dispatch) => {
    dispatch(itemLoading());
    try {
      let url = `/filters/${id}`;
      const res = await axios.delete(url);
      dispatch({ type: actionTypes.FILTERS_DELETE, id: id });
      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    } finally {
      dispatch({ type: actionTypes.UNLOAD });
    }
  };
};

let timeout;
export const filtersAutocomplete = (q) => {
  return async (dispatch) => {
    dispatch(loading());
    clearTimeout(timeout);
    try {
      let url = "/filters/autocomplete?q=";
      if (q) {
        url += q;
      }
      const promise = new Promise((resolve, reject) => {
        timeout = setTimeout(async () => {
          resolve(axios.get(url));
        }, 1000);
      });
      const res = await promise;
      dispatch({ type: actionTypes.UNLOAD });
      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    }
  };
};
