import React, { Fragment, useState } from "react";
import Drawer from "@material-ui/core/Drawer";
import {
  makeStyles,
  Divider,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Typography,
  Tooltip,
  Collapse,
} from "@material-ui/core";
import Globals from "../../util/globals";
import { useTranslation } from "react-i18next";
import { useLocation } from "react-router-dom";
import { NavLink } from "react-router-dom";
import ExpandMoreRoundedIcon from "@material-ui/icons/ExpandMoreRounded";
import { menu as _menu } from "./menu";
import { useSelector } from "react-redux";

const drawerWidth = Globals.drawerWidth;

const AppDrawer = ({ open }) => {
  const classes = useStyles();
  const { t } = useTranslation();
  const { pathname } = useLocation();
  const [subMenu, setSubmenu] = useState(0);
  const auth = useSelector((state) => state.auth);

  const getMenu = (menu, isSubmenu) => {
    return menu
      .filter((m) => m.role.includes(auth.role))
      .map((item, index) => {
        const selected =
          index === 0 && !isSubmenu
            ? pathname === "/"
            : pathname.startsWith(item.route);
        const nested =
          item.children && item.children.length ? item.children : null;

        return (
          <Fragment key={item.id}>
            <NavLink to={nested ? "#" : item.route}>
              <ListItem
                button
                divider
                selected={selected}
                className={`${classes.listBtn} ${
                  isSubmenu
                    ? classes.nested
                    : nested
                    ? classes.listBtnWithNested
                    : ""
                }`}
                onClick={() => {
                  if (subMenu === item.id) {
                    return setSubmenu(0);
                  }
                  setSubmenu(item.id);
                }}
              >
                <Tooltip
                  title={<Typography variant="caption">{item.text}</Typography>}
                  enterDelay={0}
                  placement="left"
                >
                  <ListItemIcon
                    className={selected ? classes.listItemSelected : ""}
                  >
                    {item.icon}
                    {nested && !open && (
                      <ExpandMoreRoundedIcon
                        fontSize="small"
                        className={`center-self ${
                          subMenu === item.id
                            ? classes.collapseArrow
                            : classes.nonCollapseArrow
                        }`}
                      />
                    )}
                  </ListItemIcon>
                </Tooltip>
                <ListItemText
                  primary={item.text}
                  primaryTypographyProps={{
                    variant: isSubmenu ? "body1" : "h4",
                  }}
                  className={selected ? classes.textItemSelected : ""}
                />
                {nested && (
                  <ExpandMoreRoundedIcon
                    fontSize="small"
                    className={`center-self ${
                      subMenu === item.id
                        ? classes.collapseArrow
                        : classes.nonCollapseArrow
                    } ${selected ? classes.listItemSelected : ""}`}
                  />
                )}
              </ListItem>
            </NavLink>
            {nested && (
              <Collapse
                in={subMenu === item.id || selected}
                timeout="auto"
                unmountOnExit
              >
                {getMenu(nested, true)}
              </Collapse>
            )}
          </Fragment>
        );
      });
  };

  return (
    <Drawer
      variant="permanent"
      className={`${classes.drawer} ${
        open ? classes.drawerOpen : classes.drawerClose
      }`}
      classes={{
        paper: `${classes.scrollbar} ${
          open ? classes.drawerOpen : classes.drawerClose
        }`,
      }}
    >
      <div className={classes.toolbar}>
        <Typography variant="h2" align="center">
          {t("common:menu")}
        </Typography>
      </div>
      <Divider />
      <List style={{ paddingTop: 0 }}>{getMenu(_menu)}</List>
    </Drawer>
  );
};

const useStyles = makeStyles((theme) => ({
  scrollbar: {
    "&::-webkit-scrollbar": {
      width: "0.4em",
    },
    "&::-webkit-scrollbar-track": {
      boxShadow: "inset 0 0 6px rgba(0,0,0,0.00)",
      webkitBoxShadow: "inset 0 0 6px rgba(0,0,0,0.00)",
    },
    "&::-webkit-scrollbar-thumb": {
      backgroundColor: theme.palette.primary.light,
      outline: "1px solid slategrey",
    },
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: "nowrap",
  },
  drawerOpen: {
    width: drawerWidth,
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.easeInOut,
      duration: 500,
      delay: 25,
    }),
  },
  drawerClose: {
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.easeInOut,
      duration: 500,
      // delay: 100,
    }),
    overflowX: "hidden",
    width: theme.spacing(7) + 1,
    [theme.breakpoints.up("sm")]: {
      width: theme.spacing(9) + 1,
    },
    [theme.breakpoints.down("xs")]: {
      width: 0,
    },
  },
  toolbar: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
  },
  listBtn: {
    color: theme.palette.text.disabled,
    "&.MuiListItem-button:hover": {
      background: theme.palette.primary.main,
      color: theme.palette.primary.contrastText,
    },
    "&.MuiListItem-root.Mui-selected": {
      background: theme.palette.primary.main,
    },
    "&.MuiListItem-root:hover .MuiListItemIcon-root": {
      color: theme.palette.primary.contrastText,
    },
  },
  listBtnWithNested: {
    "&.MuiListItem-root.Mui-selected": {
      background: theme.palette.primary.light,
    },
  },
  listItemSelected: {
    color: theme.palette.primary.contrastText,
  },
  textItemSelected: {
    color: theme.palette.primary.contrastText,
  },
  nested: {
    paddingLeft: theme.spacing(4),
  },
  collapseArrow: {
    transform: "rotateZ(-180deg)",
    transition: theme.transitions.create("transform", {
      easing: theme.transitions.easing.easeInOut,
      duration: 300,
      // delay: 100,
    }),
  },
  nonCollapseArrow: {
    transition: theme.transitions.create("transform", {
      easing: theme.transitions.easing.easeInOut,
      duration: 300,
      // delay: 100,
    }),
  },
}));

export default AppDrawer;
