import * as actionTypes from "./types";
import { axios } from "../../util/axiosAuth";

const loading = () => {
  return {
    type: actionTypes.COUPONS_LOADING,
  };
};
const itemLoading = () => {
  return {
    type: actionTypes.ITEM_LOADING,
  };
};
const setErrors = (err) => {
  return {
    type: actionTypes.COUPONS_ERRORS,
    errors: err,
  };
};

export const getCoupons = (query) => {
  return async (dispatch) => {
    dispatch(loading());

    try {
      let url = `/coupons`;

      let counter = 1;
      for (const key in query) {
        let operator = counter > 1 ? "&" : "?";
        url += `${operator}${key}=${query[key]}`;
        counter++;
      }
      const res = await axios.get(url);
      dispatch({ type: actionTypes.SET_COUPONS, coupons: res.data.data });
      // return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    }
  };
};

export const getCoupon = (id) => {
  return async (dispatch) => {
    dispatch(loading());
    try {
      let url = `/coupons/${id}`;
      const res = await axios.get(url);
      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    } finally {
      dispatch({ type: actionTypes.UNLOAD });
    }
  };
};

export const addEditCoupon = (body, isEdit) => {
  return async (dispatch) => {
    dispatch(loading());

    try {
      let url = "/coupons";

      let res;
      if (isEdit) {
        res = await axios.put(`${url}/${isEdit}`, body);
      } else {
        res = await axios.post(url, body);
      }
      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    } finally {
      dispatch({ type: actionTypes.UNLOAD });
    }
  };
};

export const switchCouponStatus = (id, status) => {
  return async (dispatch) => {
    dispatch(itemLoading());
    try {
      let url = `/coupons/${id}`;
      const res = await axios.patch(url, { status });
      dispatch({ type: actionTypes.COUPONS_SWITCH, id, status });
      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    } finally {
      dispatch({ type: actionTypes.UNLOAD });
    }
  };
};

export const deleteCoupon = (id) => {
  return async (dispatch) => {
    dispatch(itemLoading());
    try {
      let url = `/coupons/${id}`;
      const res = await axios.delete(url);
      dispatch({ type: actionTypes.COUPONS_DELETE, id: id });
      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    } finally {
      dispatch({ type: actionTypes.UNLOAD });
    }
  };
};
