import * as actionsTypes from "../actions/types";

const initialState = {
  specials: [],
  loading: false,
  errors: null,
};

export default (state = initialState, action) => {
  switch (action.type) {
    case actionsTypes.SPECIALS_LOADING:
      return {
        ...state,
        loading: true,
        errors: null,
      };
    case actionsTypes.SET_SPECIALS:
      return {
        ...state,
        specials: action.specials,
        loading: false,
      };

    case actionsTypes.SPECIALS_ERRORS:
      return {
        ...state,
        loading: false,
        errors: action.errors,
      };
    case actionsTypes.UNLOAD:
      return {
        ...state,
        loading: false,
      };
    default:
      return state;
  }
};
