import React, { useState, useEffect, useCallback } from "react";
import { TextField } from "@material-ui/core";
import { useTranslation } from "react-i18next";
import i18n from "../../i18n";

const Input = (props) => {
  const {
    variant,
    label,
    placeholder,
    initialValue,
    name,
    required,
    min,
    max,
    email,
    type,
    decimal,
    multiPhone,
    getResult,
    disabled,
    serverError,
    multiline,
    rows,
    setTouched,
    htmlMin,
    htmlMax,
    mathMin,
    mathMax,
    maxErrMsg,
    minErrMsg,
    step,
    withIcon,
    withStartIcon,
    forcedValue,
    onKeyDown,
    inputStyles,
    fieldStyles,
    autoFocus,
    reff,
    margin,
    helperNotice,
  } = props;

  const [state, setState] = useState({
    value: initialValue ? initialValue : "",
    touched: false,
    valid: false,
    errorMsg: "",
  });
  const { t } = useTranslation();

  const showServerErr = useCallback(() => {
    setState({
      ...state,
      valid: false,
      touched: true,
      errorMsg: serverError,
    });
    // eslint-disable-next-line
  }, [serverError]);
  useEffect(() => {
    if (serverError) {
      showServerErr();
    }
  }, [serverError, showServerErr]);

  useEffect(() => {
    if (required && setTouched && state.value === "") {
      setState({ ...state, touched: true });
    }
    // eslint-disable-next-line
  }, [setTouched]);
  useEffect(() => {
    if (forcedValue) {
      handleChange({ target: { value: forcedValue } });
      // setState({
      //   ...state,
      //   value: forcedValue,
      //   touched: true,
      //   valid: !!forcedValue
      // });
    }
    // eslint-disable-next-line
  }, [forcedValue]);
  const handleChange = (e, forced) => {
    let val = e.target.value;
    let isValid = true;
    let errorMsg = "";
    if (email) {
      const reg = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      isValid = reg.test(val) && isValid;
      errorMsg = t("common:email_err");
    }
    if (decimal) {
      const reg = /^[0-9]+(\.[0-9]*)?$/;
      isValid = reg.test(val) && isValid;
      errorMsg = t("common:onlynumbers");
    }
    if (multiPhone) {
      const reg = /^[+0-9]+(-?[0-9]\/?\+?)+$/;
      isValid = reg.test(val) && isValid;
      errorMsg = t("common:mobile_err");
    }
    if (min && val.trim().length <= min) {
      isValid = false;
      errorMsg = t("common:min_num_err", { min: min + 1 });
    }
    if (mathMin && +val <= +mathMin) {
      isValid = false;
      errorMsg = minErrMsg
        ? minErrMsg
        : t("common:math_min_err", { min: mathMin });
    }
    if (mathMax && +val > +mathMax) {
      isValid = false;
      errorMsg = maxErrMsg
        ? maxErrMsg
        : t("common:math_max_err", { max: mathMax });
    }
    if (max && val.trim().length >= max) {
      isValid = false;
      errorMsg = t("common:str_length_err", { max: max - 1 });
    }
    if (required && val.trim().length <= 0) {
      isValid = false;
      errorMsg = t("common:required_err");
    }
    setState({ value: val, touched: true, valid: isValid, errorMsg: errorMsg });
    getResult(val.trim(), isValid, name);
  };

  return (
    <TextField
      variant={variant ? variant : "outlined"}
      label={label}
      value={state.value}
      name={name}
      onChange={handleChange}
      disabled={disabled}
      error={state.touched && !state.valid}
      helperText={
        (state.touched && !state.valid && state.errorMsg) || helperNotice
      }
      type={type}
      autoFocus={autoFocus}
      multiline={multiline}
      rows={rows}
      style={{ width: "100%", margin: margin || 0 }}
      onKeyDown={onKeyDown}
      inputProps={{
        min: htmlMin,
        max: htmlMax,
        step: step,
        style: {
          ...inputStyles,
          ...((decimal || multiPhone || email) && i18n.language === "ar"
            ? inputARstyle
            : ""),
        },
      }}
      InputProps={{
        endAdornment: withIcon ? withIcon : "",
        startAdornment: withStartIcon ? withStartIcon : "",
        placeholder: placeholder ? t(`common:${placeholder}`) : "",
        style: fieldStyles,
      }}
      inputRef={reff}
    />
  );
};

const inputARstyle = {
  direction: "ltr",
  // textAlign: "right",
};

export default Input;
