import { axios } from "../../util/axiosAuth";

export const getNumbersStat = () => {
  return async (dispatch) => {
    try {
      let url = `/statistics/numbers`;

      const res = await axios.get(url);

      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      throw error;
    }
  };
};

export const getChartsStat = ({ monthly, daily }) => {
  return async (dispatch) => {
    try {
      let url = `/statistics/charts?monthly=${monthly}&daily=${daily}`;

      const res = await axios.get(url);

      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      throw error;
    }
  };
};

