import * as actionsTypes from "../actions/types";

const initialState = {
  generals: [],
  shippings: [],
  loading: false,
  itemLoading: false,
  errors: null,
};

export default (state = initialState, action) => {
  switch (action.type) {
    case actionsTypes.SETTING_LOADING:
      return {
        ...state,
        loading: true,
        errors: null,
      };

    case actionsTypes.SET_GENERALS:
      return {
        ...state,
        generals: action.generals,
        loading: false,
      };

    case actionsTypes.SET_SHIPPINGS:
      return {
        ...state,
        shippings: action.shippings,
        loading: false,
      };

    case actionsTypes.ITEM_LOADING:
      return {
        ...state,
        itemLoading: true,
      };

    case actionsTypes.SETTING_ERRORS:
      return {
        ...state,
        loading: false,
        errors: action.errors,
      };
    case actionsTypes.UNLOAD:
      return {
        ...state,
        loading: false,
        itemLoading: false,
      };
    default:
      return state;
  }
};
