import * as actionTypes from "./types";
import { axios } from "../../util/axiosAuth";

const loading = () => {
  return {
    type: actionTypes.PRODUCTS_LOADING,
  };
};
const itemLoading = () => {
  return {
    type: actionTypes.ITEM_LOADING,
  };
};
const setErrors = (err) => {
  return {
    type: actionTypes.PRODUCTS_ERRORS,
    errors: err,
  };
};

export const getProducts = (query) => {
  return async (dispatch) => {
    dispatch(loading());

    try {
      let url = "/products";
      let counter = 1;
      for (const key in query) {
        let operator = counter > 1 ? "&" : "?";
        url += `${operator}${key}=${query[key]}`;
        counter++;
      }

      const res = await axios.get(url);
      dispatch({ type: actionTypes.SET_PRODUCTS, products: res.data.data });
      return res.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    }
  };
};
export const getProduct = (id) => {
  return async (dispatch) => {
    dispatch(loading());
    try {
      let url = `/products/${id}`;

      const res = await axios.get(url);

      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    } finally {
      dispatch({ type: actionTypes.UNLOAD });
    }
  };
};
export const addEditProduct = (body, isEdit) => {
  return async (dispatch) => {
    dispatch(loading());

    try {
      let url = "/products";

      const data = new FormData();
      if (body.image.length) {
        for (const file of body.image) {
          data.append("image", file);
        }
      }

      //Keep previews image path when edit
      if (isEdit) {
        let prevImages = [];
        for (const img of body.image) {
          if (img.id.toString().startsWith("prev_")) {
            prevImages.push(img.name);
          }
        }
        data.append("prevImgs", JSON.stringify(prevImages));
      }

      delete body.image;
      for (const key in body) {
        data.append(key, body[key]);
      }
      let res;
      if (isEdit) {
        res = await axios.put(`${url}/${isEdit}`, data);
      } else {
        res = await axios.post(url, data);
      }
      dispatch({ type: actionTypes.UNLOAD });
      // dispatch({ type: actionTypes.SET_PRODUCTS, products: res.data.data });
      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    }
  };
};

export const switchProductStatus = (id, status) => {
  return async (dispatch) => {
    dispatch(itemLoading());
    try {
      let url = `/products/${id}`;
      const res = await axios.patch(url, { status });
      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    } finally {
      dispatch({ type: actionTypes.UNLOAD });
    }
  };
};

export const deleteProduct = (id) => {
  return async (dispatch) => {
    dispatch(itemLoading());
    try {
      let url = `/products/${id}`;
      const res = await axios.delete(url);
      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    } finally {
      dispatch({ type: actionTypes.UNLOAD });
    }
  };
};

let timeout;
export const productsAutocomplete = (q) => {
  return async (dispatch) => {
    dispatch(loading());
    clearTimeout(timeout);

    try {
      let url = "/products/autocomplete?q=";
      if (q) {
        url += q;
      }
      const promise = new Promise((resolve, reject) => {
        timeout = setTimeout(async () => {
          resolve(axios.get(url));
        }, 1000);
      });
      const res = await promise;
      dispatch({ type: actionTypes.UNLOAD });
      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    }
  };
};
