import * as actionTypes from "./types";
import { axios } from "../../util/axiosAuth";

const loading = () => {
  return {
    type: actionTypes.SETTING_LOADING,
  };
};
const itemLoading = () => {
  return {
    type: actionTypes.ITEM_LOADING,
  };
};
const setErrors = (err) => {
  return {
    type: actionTypes.SETTING_ERRORS,
    errors: err,
  };
};

export const getSettings = () => {
  return async (dispatch) => {
    dispatch(loading());
    try {
      let url = "/settings";
      const res = await axios.get(url);
      dispatch({ type: actionTypes.SET_GENERALS, generals: res.data.data });
      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    }
  };
};

export const saveSettings = (body) => {
  return async (dispatch) => {
    dispatch(itemLoading());
    try {
      let url = "/settings";
      const data = new FormData();
      if (body.site_logo.value.length) {
        data.append("logo", body.site_logo.value[0]);
      }

      //Keep image path if not change logo
      for (const img of body.site_logo.value) {
        if (img.id.toString().startsWith("prev_")) {
          data.append("prevImg", JSON.stringify(img.name));
        }
      }

      for (const key in body) {
        if (key === "site_logo") {
          // make value "" to prevent JSON.parse error.. image path deciede on backend if uploaded or prev
          data.append(key, JSON.stringify({ ...body[key], value: "" }));
        } else {
          data.append(key, JSON.stringify(body[key]));
        }
      }
      const res = await axios.put(url, data);
      dispatch({ type: actionTypes.SET_GENERALS, generals: res.data.data });
      return res.data.data;
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    } finally {
      dispatch({ type: actionTypes.UNLOAD });
    }
  };
};

