import * as actionsTypes from "../actions/types";

const initialState = {
  categories: {},
  loading: false,
  itemLoading: false,
  errors: null,
};
let flattedCats = [];

export default (state = initialState, action) => {
  switch (action.type) {
    case actionsTypes.CATEGORIES_LOADING:
      return {
        ...state,
        loading: true,
        errors: null,
      };

    case actionsTypes.SET_CATEGORIES:
      flattedCats = [];
      const categories = falttenCategories(action.categories.categories);
      return {
        ...state,
        categories: {
          categories: categories,
          pagination: action.categories.pagination,
        },
        loading: false,
      };
    case actionsTypes.CATEGORIES_SWITCH:
      const currentCats = state.categories.categories;
      const id = action.id;
      const status = action.status;
      const updatedStatusCats = currentCats.map((cat) => {
        if (cat.category_id === id) {
          return {
            ...cat,
            status: status,
          };
        }
        return cat;
      });
      return {
        ...state,
        categories: {
          ...state.categories,
          categories: updatedStatusCats,
        },
      };
    case actionsTypes.CATEGORIES_DELETE:
      const thisCats = state.categories.categories;
      const filteredCats = thisCats.filter(
        (cat) => cat.category_id !== action.id && cat.parent_id !== action.id
      );
      return {
        ...state,
        categories: {
          ...state.categories,
          categories: filteredCats,
        },
        itemLoading: false,
      };
    case actionsTypes.CATEGORIES_ERRORS:
      return {
        ...state,
        loading: false,
        errors: action.errors,
      };
    case actionsTypes.ITEM_LOADING:
      return {
        ...state,
        itemLoading: true,
      };
    case actionsTypes.UNLOAD:
      return {
        ...state,
        loading: false,
        itemLoading: false,
      };
    default:
      return state;
  }
};

let currentParent = "";
const falttenCategories = (categories, child) => {
  for (const cat of categories) {
    let category = { ...cat };
    if (child) {
      category.title = currentParent + category.title;
    }
    flattedCats.push(category);
    if (cat.children && cat.children.length) {
      currentParent = cat.title + " > ";
      falttenCategories(cat.children, true);
    }
  }
  return flattedCats;
};
