import * as actionTypes from "./types";
import { axios } from "../../util/axiosPublic";
import { axios as axiosAuth } from "../../util/axiosAuth";
import { createCookie, readCookie } from "../../util/cookies";
import { setAuthHeader } from "../../util/axiosAuth";

const loading = () => {
  return {
    type: actionTypes.AUTH_LOADING,
  };
};
const setErrors = (err) => {
  return {
    type: actionTypes.AUTH_ERRORS,
    errors: err,
  };
};

export const signin = (email, password) => {
  return async (dispatch) => {
    dispatch(loading());
    try {
      const res = await axios.post("/auth/signin", { email, password });
      const tok = res.data.data.token;
      const loc = res.data.data.locale;
      const role = res.data.data.role;
      const username = res.data.data.username;
      const settings = res.data.data.settings;
      createCookie(
        "user",
        JSON.stringify({
          token: tok,
          // expired: res.data.expired,
          // token_origine: res.data.token_origine,
        })
      );
      createCookie("locale", res.data.data.locale);
      setAuthHeader(res.data.data, () => {
        dispatch({
          type: actionTypes.LOGIN,
          token: tok,
          role: role,
          username: username,
          locale: loc,
          // expired: res.data.expired,
          // token_origine: res.data.token_origine,
        });
        dispatch({ type: actionTypes.SET_SETTINGS, settings });
      });
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    }
  };
};

export const logout = () => {
  return async (dispatch) => {
    try {
      await axiosAuth.post("/auth/signout");
      setAuthHeader(null, () => {
        dispatch({ type: actionTypes.LOGOUT });
      });
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    }
  };
};

export const initApp = () => {
  return async (dispatch) => {
    dispatch({ type: actionTypes.APP_LOADING });
    const user = readCookie("user");
    const locale = readCookie("locale");
    const userCookie = user ? JSON.parse(user) : null;
    try {
      let _token = userCookie ? userCookie.token : null;
      let _locale = locale ? locale : "ar";

      const res = await axiosAuth.post(
        "/auth",
        {},
        {
          headers: {
            Authorization: `Bearer ${_token}`,
            locale: _locale,
          },
        }
      );
      const tok = res.data.data.token;
      const loc = res.data.data.locale;
      const role = res.data.data.role;
      const username = res.data.data.username;
      const settings = res.data.data.settings;

      createCookie(
        "user",
        JSON.stringify({
          token: tok,
          // expired: res.data.expired,
          // token_origine: res.data.token_origine,
        })
      );
      // createCookie("locale", loc);

      setAuthHeader(res.data.data, () => {
        dispatch({
          type: actionTypes.LOGIN,
          token: tok,
          role: role,
          username: username,
          locale: loc,
          // expired: res.data.expired,
          // token_origine: res.data.token_origine,
        });
        dispatch({ type: actionTypes.SET_SETTINGS, settings });
      });
    } catch (err) {
      dispatch({ type: actionTypes.LOGOUT });
      let error;
      if (userCookie) {
        error = err.response ? err.response.data : err;
      }
      if (locale) {
        dispatch({ type: actionTypes.SET_LOCALE, locale: locale });
      }
      dispatch(setErrors(error));
      throw error;
    }
  };
};

export const requestEmailResetPass = (email) => {
  return async (dispatch) => {
    dispatch(loading());
    try {
      await axiosAuth.post("/auth/forgotpassword", { email });
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    } finally {
      dispatch({ type: actionTypes.UNLOAD });
    }
  };
};

export const validateResetPassword = (email, token) => {
  return async (dispatch) => {
    dispatch(loading());
    try {
      await axiosAuth.get(`/auth/forgotpassword/${email}/${token}`);
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    } finally {
      dispatch({ type: actionTypes.UNLOAD });
    }
  };
};

export const recreatePassword = (email, token, password) => {
  return async (dispatch) => {
    dispatch(loading());
    try {
      await axiosAuth.post(`/auth/forgotpassword/${email}/${token}`, {
        password,
      });
    } catch (err) {
      const error = err.response ? err.response.data : err;
      dispatch(setErrors(error));
      throw error;
    } finally {
      dispatch({ type: actionTypes.UNLOAD });
    }
  };
};
