import { Box, Button, Paper, Typography, useTheme } from "@material-ui/core";
import React, { useState } from "react";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";

const SingleNumberData = ({ color = "primary", text, value, to, Icon }) => {
  const theme = useTheme();
  const { t } = useTranslation();

  const [hovered, setHovered] = useState(false);
  return (
    <Paper
      style={{ background: theme.palette[color].main }}
      onMouseEnter={() => setHovered(true)}
      onMouseLeave={() => setHovered(false)}
    >
      <Box
        className="flex align-items-center flex-justify-between"
        style={{ padding: "1rem 1rem 0 1rem" }}
      >
        <Box className="flex align-items-center">
          <Icon
            style={{
              color: hovered
                ? theme.palette[color].light
                : theme.palette[color].dark,
              fontSize: 45,
              transition: "all 200ms linear",
            }}
          />
          <Typography
            className="bold"
            style={{ color: theme.palette[color].contrastText }}
          >
            {t(`common:${text}`)}
          </Typography>
        </Box>
        <Paper
          style={{
            background: hovered
              ? theme.palette[color].light
              : theme.palette[color].dark,
            transition: "all 200ms linear",
          }}
          className="py"
        >
          <Typography
            className="bold px-1"
            style={{ color: theme.palette[color].contrastText }}
          >
            {value}
          </Typography>
        </Paper>
      </Box>
      {to && (
        <Box className="flex flex-justify-end">
          <Link to={to}>
            <Button
              size="small"
              style={{ color: theme.palette[color].contrastText }}
            >
              {t("common:details")}
            </Button>
          </Link>
        </Box>
      )}
    </Paper>
  );
};

export default SingleNumberData;
