import React from "react";
import Dashboard from "./views/Dashboard/Dashborad";
import Login from "./views/Auth/Login";
import Page404 from "./views/404";

/**
 * Lazy Routes
 */
//ForgotPass
const ForgotPassword = React.lazy(() => import("./views/Auth/ForgotPassword"));
//Profile
const Profile = React.lazy(() => import("./views/Profile/Profile"));
// Products
const Products = React.lazy(() => import("./views/Products/Products"));
const AddEditProduct = React.lazy(() => import("./views/Products/AddEdit"));
const ViewProduct = React.lazy(() => import("./views/Products/View"));
// Categories
const Categories = React.lazy(() => import("./views/Categories/Categories"));
const AddEditCategory = React.lazy(() => import("./views/Categories/AddEdit"));
// Filters
const Filters = React.lazy(() => import("./views/Filters/Filters"));
const AddEditFilter = React.lazy(() => import("./views/Filters/AddEdit"));
// Attributes
const Attributes = React.lazy(() => import("./views/Attributes/Attributes"));
const AddEditAttribute = React.lazy(() => import("./views/Attributes/AddEdit"));
// Coupons
const Coupons = React.lazy(() => import("./views/Coupons/Coupons"));
const AddEditCoupon = React.lazy(() => import("./views/Coupons/AddEdit"));
// Orders
const Orders = React.lazy(() => import("./views/Orders/Orders"));
const ViewOrder = React.lazy(() => import("./views/Orders/View"));
// Users
const Users = React.lazy(() => import("./views/Users/Users"));
// Push Notif
const PushNotifications = React.lazy(() =>
  import("./views/PushNotifications/PushNotifications")
);
// Settigns
const GeneralSettings = React.lazy(() =>
  import("./views/Settings/Generals/Generals")
);
const Shippings = React.lazy(() =>
  import("./views/Settings/Shipping/Shipping")
);
const Languages = React.lazy(() =>
  import("./views/Settings/Languages/Languages")
);
const AddEditLanguage = React.lazy(() =>
  import("./views/Settings/Languages/AddEdit")
);
const Taxes = React.lazy(() => import("./views/Settings/Tax/Tax"));
const AddEditTax = React.lazy(() => import("./views/Settings/Tax/AddEdit"));
const Admins = React.lazy(() => import("./views/Settings/Admins/Admins"));
const AddEditAdmins = React.lazy(() =>
  import("./views/Settings/Admins/AddEdit")
);
